import React, { Component } from 'react';
import {connect} from "react-redux";
import PropTypes from 'prop-types';
import "./Header.css";

function mapStateToProps(state) {
	return {
		ui: state.main.ui
	}
}

class Header extends Component {
	render() {
		const ui = this.props.ui;
		
		return (
			<header className="header">
				{/* Header logotype and main header name of the site */}
				<div className="logo_wrap">
					<div className="img_wrap">
						<img src="images/header/header_logo.png" alt="pocket-friend logotype" />
					</div>
					<div>
						<h2>{ui.header.title1}</h2>
						<h2>{ui.header.title2}</h2>
					</div>
				</div>
				{/* Header main decoration image */}
				<div className="wrapper center">
					<div className="back_foto">
						<img src="images/header/header_back_foto.png" alt="header foto" />
					</div>
				</div>
			</header>
		)
	}
}

Header.propTypes = {
	ui: PropTypes.shape({
		header: PropTypes.shape({
			title1: PropTypes.string.isRequired,
			title2: PropTypes.string.isRequired
		})
	})
}

export default connect(mapStateToProps)(Header);