import React, { Component } from 'react';
import Slider from "react-slick";
import {connect} from "react-redux";
import {Link} from "react-router";
import PropTypes from "prop-types";

import actions from "../../actions";

import "../../../node_modules/slick-carousel/slick/slick.css";
import "../../../node_modules/slick-carousel/slick/slick-theme.css";
import "./SliderSection.css";

function mapStateToProps(state) {
	return {
		language: state.main.language,
		width: state.main.width
	}
}

class SliderSection extends Component {
	onClick = (id) => {
		this.props.dispatch(actions.setProductPageId(id));
	}

	render() {
		const width = this.props.width;
		let settings = {
	      infinite: true,
	      slidesToShow: 3,
	      slidesToScroll: 1,
	      swipeToSlide: true,
	      autoplay: true,
	      speed: 300,
	      autoplaySpeed: 3000,
	      pauseOnHover: true,
	      cssEase: "ease",
	      adaptiveHeight: true
	    };

		if (width <= 1140) {
			settings.slidesToShow = 2;
		}
		if (width <= 740) {
			settings.slidesToShow = 1;
		}
		if (width <= 440) {
			settings.arrows = false;
		}

	    const language = this.props.language;
	    const products = this.props.products;

		return (
			<div className="slider_section">
				<h2 className="block_header">{this.props.slider_header}</h2>
				<div className="slider_body">
					<Slider {...settings}>
			          	{
			          		products.map((element, index) => {
			          			const header = element.lang[language].title;
			          			const imgPath = element.images[0] + "s.jpg";
			          			return (
			          				<div key={index} className="slider_item">
			          					<Link to="/product" onClick={() => {this.onClick(element.id)}}>
			          						<h4 className="slide_header">{header}</h4>
			          					</Link>
				          				<div className="slider_image">
				          					<img src={imgPath} alt="img not loaded"/>
				          				</div>
			          				</div>
			          			)
			          		})
			          	}
			        </Slider>
		        </div>
			</div>
		)
	}
}

SliderSection.propTypes = {
	products: PropTypes.array.isRequired,
	slider_header: PropTypes.string.isRequired,
	width: PropTypes.number.isRequired
}

export default connect(mapStateToProps)(SliderSection);