import React, { Component } from 'react';
import {connect} from "react-redux";
import {Link} from "react-router";
import PropTypes from 'prop-types';
import { FaFacebookF, FaInstagram, FaViber, FaRegEnvelope } from "react-icons/fa";

import "./Footer.css";

function mapStateToProps(state) {
	return {
		ui: state.main.ui
	}
}

class Footer extends Component {
	render() {
		const ui = this.props.ui;

		return (
			<div className="footer">
				<div className="footer_leftblock">
					<Link to="/">
						<div className="footer_logo">
							<div className="img_wrap">
								<img src="images/header/header_logo.png" alt="pocket-friend logotype" />
							</div>
							<div className="footer_logo_title">
								<h2>{ui.header.title1}</h2>
								<h2>{ui.header.title2}</h2>
							</div>
						</div>
					</Link>
					<div className="footer_menu">
						<ul className="list">
							<li><Link to="/">
								<div>
									{ui.mainMenu.main}
								</div>
							</Link></li>
							<li><Link to="/gallery">
								<div>
									{ui.mainMenu.gallery}
								</div>
							</Link></li>
							<li><Link to="/favorits">
								<div>
									{ui.mainMenu.favorits}
								</div>
							</Link></li>
							<li><Link to="/news">
								<div>
									{ui.mainMenu.news}
								</div>
							</Link></li>
							<li><Link to="/articles">
								<div>
									{ui.mainMenu.articles}
								</div>
							</Link></li>
							<li><Link to="/contacts">
								<div>
									{ui.mainMenu.contacts}
								</div>
							</Link></li>
						</ul>
					</div>
				</div>
				<div className="links">
					<a href="https://www.facebook.com/karmanniydroog/">
						<div>
							<FaFacebookF />
						</div>
					</a>
					<a href="https://www.instagram.com/karmanniy_droog/">
						<div>
							<FaInstagram />
						</div>
					</a>
					<a href="tel:+380676995858">
						<div>
							<FaViber />
						</div>
					</a>
					<a href="mailto: ivanglemyaznyy@gmail.com">
						<div>
							<FaRegEnvelope />
						</div>
					</a>
				</div>
			</div>
		)
	}
}

Footer.propTypes = {
	ui: PropTypes.shape({
		header: PropTypes.shape({
			title1: PropTypes.string.isRequired,
			title2: PropTypes.string.isRequired
		})
	})
}

export default connect(mapStateToProps)(Footer);