import React, { Component } from 'react';
import {createStore, applyMiddleware} from "redux";
import {Provider, connect} from "react-redux";
import thunk from "redux-thunk";
import {Router, Route, browserHistory, IndexRoute} from "react-router";
import {syncHistoryWithStore} from "react-router-redux";

import allReducers from "./reducers";
import actions from "./actions/actions";

import MainPage from "./components/MainPage";
import GalleryPage from "./components/GalleryPage";
import FavoritsPage from "./components/FavoritsPage";
import ProductPage from "./components/ProductPage";
import NewsPage from "./components/NewsPage";
import ArticlesPage from "./components/ArticlesPage";
import ContactsPage from "./components/ContactsPage";
import PostPage from "./components/PostPage";
import ResolutionShow from "./components/resolutionShow.js"

import "./App.css";

// Main local store
var middlewares = applyMiddleware(thunk);
var store = createStore(allReducers, middlewares);

var history = syncHistoryWithStore(browserHistory, store);

function mapStateToProps(state) {
    return {
        data: state.main.database,
        dataLoaded: state.main.dataLoaded,
        width: state.main.width,
        height: state.main.height
    }
}

class RootComponent extends Component {
    constructor(props) {
        super(props);
        setTimeout(this.init, 1000);
    }

    componentDidMount() {
        window.addEventListener('resize', this.handleResize);
    };

    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize);
    }

    handleResize = () => {
        const width = window.innerWidth;
        const height = window.innerHeight;
        this.props.dispatch(actions.setWindowResize(width, height));
    }

    init = () => {
        this.props.dispatch(actions.siteInit());
    }

    render() {
        //console.log(this.props);
        return (
            <div className="wrapper_main">
                <ResolutionShow />
                {
                    this.props.dataLoaded ?
                    this.props.children :
                    <div>data is loading</div>
                }
            </div>
          )     
    }
}

RootComponent = connect(mapStateToProps)(RootComponent);

class App extends Component {
    render() {       
        return (
            <Provider store={store}>
                <Router history={history}>
                    <Route path="/" component={RootComponent}>
                        <IndexRoute component={MainPage}/>
                        <Route path="gallery" component={GalleryPage} />
                        <Route path="favorits" component={FavoritsPage} />
                        <Route path="product" component={ProductPage} />
                        <Route path="news" component={NewsPage} />
                        <Route path="articles" component={ArticlesPage} />
                        <Route path="contacts" component={ContactsPage} />
                        <Route path="post" component={PostPage} />
                    </Route>
                </Router>
            </Provider>
        );
    }
}

export default App;