import React, { Component } from 'react';
import {connect} from "react-redux";
import {Link} from "react-router";
import {Element} from 'react-scroll';
import PropTypes from 'prop-types';

import SliderSection from "../SliderSection";

import "./InfoBlock.css";

function mapStateToProps(state) {
	return {
		ui: state.main.ui,
		bestSellers: state.main.bestSellers
	}
}

class InfoBlock extends Component {     
	render() {  
		const blockStyle = "mainpage_block info_block " + this.props.blockstyle;
		const infoBlockUi = this.props.ui.mainPage.infoBlock;

		return ( 
			<Element name="info_block" className="element">
				<div className={blockStyle}>
					<div className="main_column">
						<p className="description">{infoBlockUi.description}<br/>
						</p>
					</div>
					<SliderSection slider_header={infoBlockUi.header} products={this.props.bestSellers}/>
					<div className="main_column">
						<Link to="/contacts">
							<div className="next button">{infoBlockUi.toContactsButton}</div>
						</Link>
					</div>
					<div className="clear_fix"></div>
				</div>
			</Element>
		)     
	} 
}

InfoBlock.propTypes = {
	bestSellers: PropTypes.array.isRequired,
	blockstyle: PropTypes.string.isRequired,
	ui: PropTypes.shape({
		mainPage: PropTypes.shape({
			infoBlock: PropTypes.shape({
				description: PropTypes.string.isRequired,
				header: PropTypes.string.isRequired,
				toContactsButton: PropTypes.string.isRequired
			})
		})
	})
}

export default connect(mapStateToProps)(InfoBlock);