import React, { Component } from "react";
import {connect} from "react-redux";
import PropTypes from 'prop-types';

import MainMenu from "../MainMenu";
import SideMainMenu from "../SideMainMenu";
import PostBlock from "../PostBlock";
import Footer from "../Footer";

import "./ArticlesPage.css";

function mapStateToProps(state) {
	return {
		data: state.main.database,
		height: state.main.height
	}
}

class ArticlesPage extends Component {
	render() {
		let articles = this.props.data.articles.slice();
		let posts = articles.map((element) => {
			return this.props.data.posts[element];
		});

		const blockHeight = this.props.height - 160;
		const articlesPageStyle = {minHeight: blockHeight + "px"};

		return (
			<div className="articles_page">
				<MainMenu active="artictes" />
				<SideMainMenu />
				<div  style={articlesPageStyle}>
					<h2>ARTICLES PAGE</h2>
					<div className="main_column">
						{
							posts.map((element, index) => {
								return <PostBlock key={index} post={element} blockstyle={"style" + (1 + index % 2)}/>
							})
						}
					</div>
				</div>
				<Footer />
			</div>
		)
	}
}

ArticlesPage.propTypes = {
	data: PropTypes.object.isRequired
}

export default connect(mapStateToProps)(ArticlesPage);