import products from "./products.js";
import groups from "./groups.js";
import uiLang from "./uiLang.js";
import news from "./news.js";
import articles from "./articles.js";
import posts from "./posts.js";

var database = {
	products: [...products],
	groups: groups,
	uiLang: uiLang,
	defaultLanguage: "EN",
	news: news,
	articles: articles,
	posts: posts
}

export default database;