const actions = {

	//imitation of initial loading resources from server 
	siteInit: () => {
		return {
			type: "SITE_INITED",
			payload: null
		}
	},

	// change language on site
	changeLanguage: (value) => {
		return {
			type: "LANGUAGE_CHANGED",
			payload: value
		}
	},

	// change gallery filter checkboxes
	filterChange: (name, value) => {
		const params = {
			name,
			value
		};
		return {
			type: "FILTER_CHECKBOX_CHANGED",
			payload: params
		}
	},

	// change gallery search/filter string
	searchChange: (value) => {
		return {
			type: "SEARCH_CHANGED",
			payload: value
		}
	},

	// toggle gallery filter menu
	toggleFilterMenu: () => {
		return {
			type: "FILTER_MENU_TOGGLED",
			payload: null
		}
	},

	// change filter method in gallery
	changeFilterMethod: (value) => {
		return {
			type: "FILTER_METHOD_CHANGED",
			payload: value
		}
	},

	// add item to user favorits list
	addToFavorits: (itemId) => {
		return {
			type: "ITEM_ADDED_TO_FAVORITS",
			payload: itemId
		}
	},

	// remove item from user favorits list
	removeFromFavorits: (itemId) => {
		return {
			type: "ITEM_REMOVED_FROM_FAVORITS",
			payload: itemId
		}
	},

	// set gallery page value (for paginate)
	setGalleryPage: (value) => {
		return {
			type: "GALLERY_PAGE_SET",
			payload: value
		}
	},

	// set product id for personal product page
	setProductPageId: (value) => {
		return {
			type: "PRODUCT_PAGE_ID_SET",
			payload: value
		}
	},

	// set post id for personal post page
	setPostId: (id) => {
		return {
			type: "POST_PAGE_ID_SET",
			payload: id
		}
	},

	//set new user browser inner vindow values
	setWindowResize: (width, height) => {
		return {
			type: "WINDOW_RESIZED",
			payload: {
				width,
				height
			}
		}
	}
}

export default actions;