import React, { Component } from 'react';
import {connect} from "react-redux";
import {Link} from "react-router";
import PropTypes from 'prop-types';

import actions from "../../actions";

import "./PostBlock.css";

function mapStateToProps(state) {
	return {
		ui: state.main.ui,
		language: state.main.language
	}
}

class PostBlock extends Component {
	onClick(id) {
		this.props.dispatch(actions.setPostId(id));
		window.scroll(0, 0);
	}

	render() {
		const ui = this.props.ui;
		const language = this.props.language;
		const blockClass = "post_block " + this.props.blockstyle; 
		let date = new Date(this.props.post.date);
		const hours = (date.getHours() < 10) ? ("0" + date.getHours()) : date.getHours();
		const minutes = (date.getMinutes() < 10) ? ("0" + date.getMinutes()) : date.getMinutes();
		let stringDate = "(" + 	date.getDate() + " " + 
								ui.month[date.getMonth()] + " " + 
								date.getFullYear() + " " + 
								hours + ":" +
								minutes + ")";
		const header = this.props.post.lang[language].header;
		const imgUrl = this.props.post.images[0] + "s.jpg";
		let text = this.props.post.lang[language].text.slice();

		return (
			<div className={blockClass}>
				<div className="post_block_body">
					<div className="header_time_block">
						<h3 className="post_header">{header}</h3>
						<p className="post_time">{stringDate}</p>
					</div>
					<div className="post_img_wrap">
						<img src={imgUrl} alt={header} />
					</div>
					<div className="post_text_wrap">
						{
							text.map((element, index) => {
								return <p key={index}>{element}</p>
							})
						}
					</div>
				</div>
				<Link to="/post" onClick={() => {this.onClick(this.props.post.id)}}>
					<div className="to_post button">TO POST</div>
				</Link>
				<div className="clear_fix"></div>
			</div>
		)
	}
}

PostBlock.propTypes = {
	post: PropTypes.object.isRequired
}

export default connect(mapStateToProps)(PostBlock);