import React, { Component } from "react";
import {Link} from "react-router";
import {connect} from "react-redux";
import PropTypes from "prop-types";

import actions from "../../actions";

import "./MainMenu.css";

function mapStateToProps(state) {
	return {
		ui: state.main.ui,
		languages: state.main.languages,
		language: state.main.language
	}
}

class MainMenu extends Component {

	changeLanguage = (event) => {
		this.props.dispatch(actions.changeLanguage(event.target.value));
	}

	render() {
		const ui = this.props.ui;
		const active = this.props.active;

		return (
			<div className="main_menu">
				<div className="main_menu_wrapper">
					<ul>
						<li><Link to="/">
							<div className="button_wrap">
								<div className={"button" + (active === "main" ? " active" : "")}>
									{ui.mainMenu.main}
								</div>
							</div>
						</Link></li>
						<li><Link to="/gallery">
							<div className="button_wrap">
								<div className={"button gallery" + (active === "gallery" ? " active" : "")}>
									{ui.mainMenu.gallery}
								</div>
							</div>
						</Link></li>
						<li><Link to="/favorits">
							<div className="button_wrap">
								<div className={"button" + (active === "favorits" ? " active" : "")}>
									{ui.mainMenu.favorits}
								</div>
							</div>
						</Link></li>
						<li><Link to="/news">
							<div className="button_wrap">
								<div className={"button" + (active === "news" ? " active" : "")}>
									{ui.mainMenu.news}
								</div>
							</div>
						</Link></li>
						<li><Link to="/articles">
							<div className="button_wrap">
								<div className={"button" + (active === "artictes" ? " active" : "")}>
									{ui.mainMenu.articles}
								</div>
							</div>
						</Link></li>
						<li><Link to="/contacts">
							<div className="button_wrap">
								<div className={"button" + (active === "contacts" ? " active" : "")}>
									{ui.mainMenu.contacts}
								</div>
							</div>
						</Link></li>
					</ul>
					<div className="lang_select">
						<span>{ui.mainMenu.language} :</span>
						<select id="languageSelect" value={this.props.language} onChange={this.changeLanguage}>
							{
								this.props.languages.map((el, i) => {
									return <option key={i} value={el}>{el}</option>
								})
							}
						</select>
					</div>
				</div>
			</div>
		);
	}
}

MainMenu.propTypes = {
	ui: PropTypes.shape({
		mainMenu: PropTypes.shape({
			main: PropTypes.string.isRequired,
			gallery: PropTypes.string.isRequired,
			favorits: PropTypes.string.isRequired
		})
	}),
	languages: PropTypes.array.isRequired,
	language: PropTypes.string.isRequired,
	active: PropTypes.string.isRequired
}

export default connect(mapStateToProps)(MainMenu);