import React, { Component } from 'react';
import {connect} from "react-redux";
import PropTypes from 'prop-types';

import Header from "../Header";
import MainMenu from "../MainMenu";
import SideMainMenu from "../SideMainMenu";
import GalleryBlock from "../GalleryBlock";
import NewsBlock from "../NewsBlock";
import ArticleBlock from "../ArticleBlock";
import InfoBlock from "../InfoBlock";
import Footer from "../Footer";

import "./MainPage.css";

function mapStateToProps(state) {
	return {
		data: state.main.database
	}
}

class MainPage extends Component {
	render() {
		let lastNews = this.props.data.posts[this.props.data.news[0]];
		let lastArticle = this.props.data.posts[this.props.data.articles[0]];

		return (
			<div>
				<Header />
				<MainMenu active="main"/>
				<SideMainMenu />
				<div className="main_section_wrapper">
					<GalleryBlock blockstyle="style1"/>
					<NewsBlock post={lastNews} blockstyle="style2"/>
					<ArticleBlock post={lastArticle} blockstyle="style1"/>
					<InfoBlock blockstyle="style2"/>
				</div>
				<Footer />
			</div>
		)
	}
}

MainPage.propTypes = {
	data: PropTypes.object.isRequired
}

export default connect(mapStateToProps)(MainPage);