import React, { Component } from 'react';
import {connect} from "react-redux";
import {FaArrowLeft, FaArrowRight} from "react-icons/fa";

import actions from "../../actions";

import "./PagingSection.css";

function mapStateToProps(state) {
	return {
		page: state.main.page,
		perPage: state.main.perPage
	}
}

class PagingSection extends Component {

	onPageClick = (targetPage) => {
		const page = parseInt(this.props.page);
		if (page !== targetPage) {
			window.scroll(0, 0);
			this.props.dispatch(actions.setGalleryPage(targetPage));
		}
	}

	setPageList = () => {
		var pagesRange = 2;
		var currPage = this.props.page;
		var lastPage = this.props.lastPage;
		var pageStart = currPage - pagesRange >= 0 ? currPage - pagesRange : 0;
		var pageEnd = currPage + pagesRange <= lastPage ? currPage + pagesRange : lastPage;
		var tempPage = pageStart;
		var result = [];
		do {
			result.push(tempPage);
			tempPage += 1;
		} while (tempPage < pageEnd);
		return result;
	}

	render() {
		const pages = this.setPageList();
		const currentPage = parseInt(this.props.page);
		const lastPage = this.props.lastPage;

		return (
			<div className="paging_section">
				<div className="pages">
					{
						(currentPage > 0) &&
						<div className="page" onClick={() => {this.onPageClick(currentPage - 1)}}>
							<FaArrowLeft />
						</div>
					}
					{
						pages.map((element, index) => {
							var pageClass="page";
							var page = currentPage;
							if (element === page) {
								pageClass += " current";
							}
							return (
								<div key={index} 
									className={pageClass} 
									onClick={() => {this.onPageClick(index)}}
								>
									{element + 1}
								</div>
							)
						})
					}
					{
						(currentPage + 1 < lastPage) &&
						<div className="page" onClick={() => {this.onPageClick(currentPage + 1)}}>
							<FaArrowRight />
						</div>
					}					
				</div>
			</div>
		)
	}
}

export default connect(mapStateToProps)(PagingSection);