import React, { Component } from "react";
import {connect} from "react-redux";
import PropTypes from 'prop-types';

import MainMenu from "../MainMenu";
import SideMainMenu from "../SideMainMenu";
import PostBlock from "../PostBlock";
import Footer from "../Footer";

import "./NewsPage.css";

function mapStateToProps(state) {
	return {
		data: state.main.database,
		height: state.main.height
	}
}

class NewsPage extends Component {
	render() {
		let news = this.props.data.news.slice();
		let posts = news.map((element) => {
			return this.props.data.posts[element];
		});

		const blockHeight = this.props.height - 160;
		const newsPageStyle = {minHeight: blockHeight + "px"};

		return (
			<div className="news_page" style={newsPageStyle}>
				<MainMenu active="news" />
				<SideMainMenu />
				<h2>NEWS PAGE</h2>
				<div className="main_column">
					{
						posts.map((element, index) => {
							return <PostBlock key={index} post={element} blockstyle={"style" + (1 + index % 2)}/>
						})
					}
				</div>
				<Footer />
			</div>
		)
	}
}

NewsPage.propTypes = {
	data: PropTypes.object.isRequired
}

export default connect(mapStateToProps)(NewsPage);