import React, { Component } from 'react';

import MainMenu from "../MainMenu";
import SideMainMenu from "../SideMainMenu";
import FavoritsSection from "../FavoritsSection";
import Footer from "../Footer";

import "./FavoritsPage.css";

class FavoritsPage extends Component {
	render() {
		return (
			<div className="favorits_page">
				<MainMenu active="favorits" />
				<SideMainMenu />
				<FavoritsSection />
				<Footer />
			</div>
		)
	}
}

export default FavoritsPage;