import React, { Component } from 'react';
import {connect} from "react-redux";
import PropTypes from "prop-types";

import "./SingleSlider.css";

function mapStateToProps(state) {
	return {
		language: state.main.language,
		width: state.main.width
	}
}

class SingleSlider extends Component {
	constructor(props) {
		super(props);
		this.state = {
			currentImg: 0
		}
	}

	onClick = (index) => {
		this.setState({currentImg: index});
	}

	render() {
		const images = this.props.images;
		let currentImg = this.state.currentImg;

		if (currentImg > images.length - 1) {
			currentImg = 0;
			this.setState({currentImg: 0});
		}

		let currImgUrl = images[currentImg] + ".jpg";
		
		return (
			<div className="single_slider">
				<div className="main_img">
					<img src={currImgUrl} alt="pic"/>
				</div>
				{
					images.length > 1 && 
					<div className="previews">
						{
							images.map((element, index) => {
								const imgUrl = element + "s.jpg";
								const className = (index === currentImg) ? "img_prev" : "img_prev grey";
								return (
									<div key={index} 
										className={className} 
										onClick={() => {this.onClick(index)}}
									>
										<img src={imgUrl} alt="pic"/>
									</div>
								)
							})
						}
					</div>
				}
			</div>
		)
	}
}

SingleSlider.propTypes = {
	images: PropTypes.array.isRequired
}

export default connect(mapStateToProps)(SingleSlider);