import React, { Component } from 'react';
import {Link} from "react-router";
import {connect} from "react-redux";
import PropTypes from "prop-types";

import SliderSection from "../SliderSection";
import Arrow from "../Arrow";

import "./GalleryBlock.css";

function mapStateToProps(state) {
	return {
		ui: state.main.ui,
		newProducts: state.main.newProducts
	}
}

class GalleryBlock extends Component {
	render() {
		const blockStyle = "mainpage_block gallery_block " + this.props.blockstyle;
		const galleryBlockUi = this.props.ui.mainPage.galleryBlock;
		const next = this.props.ui.next;

		return (
			<div className={blockStyle}>
				<div className="main_column">
					<p className="description">
						{galleryBlockUi.description}
						<br/>
					</p>
				</div>
				<SliderSection slider_header={galleryBlockUi.header} products={this.props.newProducts}/>
				<div className="main_column">
					<Link to="/gallery">
						<div className="next button">{galleryBlockUi.toGalleryButton}</div>
					</Link>
				</div>
				<div className="clear_fix"></div>
				<Arrow destination="news_block" blockstyle={this.props.blockstyle} next={next}/>
			</div>
		)
	}
}

GalleryBlock.propTypes = {
	newProducts: PropTypes.array.isRequired,
	blockstyle: PropTypes.string.isRequired,
	ui: PropTypes.shape({
		mainPage: PropTypes.shape({
			galleryBlock: PropTypes.shape({
				description: PropTypes.string.isRequired,
				header: PropTypes.string.isRequired,
				toGalleryButton: PropTypes.string.isRequired
			})
		}),
		next: PropTypes.string.isRequired
	})
}

export default connect(mapStateToProps)(GalleryBlock);