import React, { Component } from 'react';
import {connect} from "react-redux";
import { FaRegHeart, FaHeart } from "react-icons/fa";

import actions from "../../actions";

import MainMenu from "../MainMenu";
import SideMainMenu from "../SideMainMenu";
import SingleSlider from "../SingleSlider";
import Footer from "../Footer";

import "./ProductPage.css";

function mapStateToProps(state) {
	return {
		ui: state.main.ui,
		language: state.main.language,
		products: state.main.products,
		productPage: state.main.productPage
	}
}

class ProductPage extends Component {
	toggleFavoritItem = (product) => {
		if (product.favorits) {
			this.props.dispatch(actions.removeFromFavorits(product.id));
		} 
		else {
			this.props.dispatch(actions.addToFavorits(product.id));
		}
	}

	goBack = () => {
		this.props.router.goBack();
	}

	render() {
		const ui = this.props.ui;
		const productPage = parseInt(this.props.productPage)
		const product = this.props.products.find((element) => {
			return element.id === productPage;
		});

		let language = this.props.language;

		if (!product.lang[this.props.language]) {
			language = "EN";
		}

		const txt = product.lang[language];
		const productID = this.props.ui.galleryPage.gallerySection.galleryItem.productId;
		const favoritsClass = "favorits_button" + (product.favorits ? " remove" : " add");

		return (
			<div className="product_page">
				<MainMenu active="" />
				<SideMainMenu />
				<div className="main_column">
					<div className="title">{txt.title}</div>
					<SingleSlider images={product.images} />
					<div className="params_panel">
						<div className="prodId">{productID + ": " + product.id}</div>
						<div className="price">{product.price + " " + txt.currency}</div>
						<div 
							className={favoritsClass} 
							onClick={() => {this.toggleFavoritItem(product)}}
							title="Add to favorits"
						>
							{
								product.favorits ?
								<FaHeart /> : 
								<FaRegHeart />
							}
						</div>
					</div>
					<div className="text_section">
						<p>{txt.text}</p>
					</div>
					<div className="back_button" onClick={this.goBack}>{ui.back}</div>
				</div>
				<Footer />
			</div>
		)
	}
}

export default connect(mapStateToProps)(ProductPage);