import React, { Component } from 'react';
import {connect} from "react-redux";
import PropTypes from 'prop-types';

import MainMenu from "../MainMenu";
import SideMainMenu from "../SideMainMenu";
import Footer from "../Footer";

import "./ContactsPage.css";

function mapStateToProps(state) {
	return {
		data: state.main.database,
		height: state.main.height
	}
}

class ContactsPage extends Component {
	render() {
		const blockHeight = this.props.height - 160;
		const contactsPageStyle = {minHeight: blockHeight + "px"};

		return (
			<div className="contacts_page">
				<MainMenu active="contacts" />
				<SideMainMenu />
				<div style={contactsPageStyle}>
					<h2>CONTACTS</h2>
				</div>
				<Footer />
			</div>
		)
	}
}

ContactsPage.propTypes = {
	data: PropTypes.object.isRequired
}

export default connect(mapStateToProps)(ContactsPage);