import React, { Component } from 'react';
import {connect} from "react-redux";
import PropTypes from 'prop-types';

import MainMenu from "../MainMenu";
import SingleSlider from "../SingleSlider";
import Footer from "../Footer";

import "./PostPage.css";

function mapStateToProps(state) {
	return {
		ui: state.main.ui,
		data: state.main.database,
		post: state.main.post,
		language: state.main.language,
		height: state.main.height
	}
}

class PostPage extends Component {
	goBack = () => {
		this.props.router.goBack();
	}

	render() {
		const ui = this.props.ui;
		const language = this.props.language;

		let post, date, header, imgUrl, text, hours, minutes, stringDate;

		if (this.props.post !== "-1") {
			post = this.props.data.posts[this.props.post];
			date = new Date(post.date);
			hours = (date.getHours() < 10) ? ("0" + date.getHours()) : date.getHours();
			minutes = (date.getMinutes() < 10) ? ("0" + date.getMinutes()) : date.getMinutes();
			stringDate = "(" + 	date.getDate() + " " + 
								ui.month[date.getMonth()] + " " + 
								date.getFullYear() + " " + 
								hours + ":" +
								minutes + ")";
			header = post.lang[language].header;
			text = post.lang[language].text.slice();
		}

		const blockHeight = this.props.height - 160;
		const postSectStyle = {minHeight: blockHeight + "px"};

		return(
			<div className="post_page">
				<MainMenu active=""/>
				<div className="main_column post_section" style={postSectStyle}>
					{
						post ? 
						<div className="body">
							<h3 className="post_header">{header}</h3>
							<p className="post_time">{stringDate}</p>

							<SingleSlider images={post.images} />
							<div className="text_section" > 
							{
								text.map((element, index) => {
									return <p key={index}>{element}</p>
								})
							}
							</div>
						</div>
						:
						<div></div>
					}
					<div className="back_button" onClick={this.goBack}>{ui.back}</div>
				</div>
				<Footer />
			</div>
		)
	}
}

PostPage.propTypes = {
	data: PropTypes.object.isRequired
}

export default connect(mapStateToProps)(PostPage);