import React, { Component } from 'react';
import {connect} from "react-redux";
import {Link} from "react-router";
import {Element} from 'react-scroll';
import PropTypes from 'prop-types';

import PostBlock from "../PostBlock";
import Arrow from "../Arrow";

import "./NewsBlock.css";

function mapStateToProps(state) {
	return {
		ui: state.main.ui
	}
}

class NewsBlock extends Component {
	render() {
		const blockStyle = "mainpage_block news_block " + this.props.blockstyle;
		const newsBlockUi = this.props.ui.mainPage.newsBlock;
		const next = this.props.ui.next;

		return (
			<Element name="news_block" className="element">
				<div className={blockStyle}>
					<div className="main_column">
						<p className="description">
							{newsBlockUi.description}
							<br/>
						</p>
						<h3 className="block_header">{newsBlockUi.header}</h3>
						<PostBlock post={this.props.post} blockstyle={this.props.blockstyle}/>
						<Link to="/news">
							<div className="next button">{newsBlockUi.toNewsButton}</div>
						</Link>
						<div className="clear_fix"></div>
					</div>
					<Arrow destination="article_block" blockstyle={this.props.blockstyle} next={next}/>
				</div>
			</Element>
		)
	}
}

NewsBlock.propTypes = {
	post: PropTypes.object.isRequired,
	blockstyle: PropTypes.string.isRequired,
	ui: PropTypes.shape({
		mainPage: PropTypes.shape({
			newsBlock: PropTypes.shape({
				description: PropTypes.string.isRequired,
				header: PropTypes.string.isRequired,
				toNewsButton: PropTypes.string.isRequired
			})
		}),
		next: PropTypes.string.isRequired
	})
}

export default connect(mapStateToProps)(NewsBlock);