import React, { Component } from 'react';
import {connect} from "react-redux";
import {Link} from "react-router";
import { FaRegHeart, FaHeart } from "react-icons/fa";

import actions from "../../actions";

import "./GalleryItem.css";

function mapStateToProps(state) {
	return {
		products: state.main.products,
		language: state.main.language,
		ui: state.main.ui
	}
}

class GalleryItem extends Component {
	toggleFavoritItem = (product) => {
		if (product.favorits) {
			this.props.dispatch(actions.removeFromFavorits(product.id));
		} 
		else {
			this.props.dispatch(actions.addToFavorits(product.id));
		}
	} 

	onClick = (id) => {
		this.props.dispatch(actions.setProductPageId(id));
		window.scroll(0, 0);
	}

	render() {
		const ui = this.props.ui;
		const product = this.props.products.find((element) => {
			return element.id === this.props.prodId;
		});

		let language = this.props.language;

		if (!product.lang[this.props.language]) {
			language = "EN";
		}

		const txt = product.lang[language];
		const img = product.images[0] + "s.jpg";
		const productID = this.props.ui.galleryPage.gallerySection.galleryItem.productId;

		return (
			<div className="gallery_item">
				<div className="title">{txt.title}</div>
				<img src={img} alt="img"/>
				<div className="prodId">{productID + ": " + product.id}</div>
				<div className="price">{product.price + " " + txt.currency}</div>
				<Link to="/product" onClick={() => {this.onClick(product.id)}}>
					<div className="to_page">Go</div>
				</Link>
				{
					product.favorits ?
					<div 
						className="favorit_button remove" 
						onClick={() => {this.toggleFavoritItem(product)}}
						title={ui.removeFromFavorits}
					>
						<FaHeart />
					</div> 
					:
					<div 
						className="favorit_button add" 
						onClick={() => {this.toggleFavoritItem(product)}}
						title={ui.addToFavorits}
					>
						<FaRegHeart />
					</div> 
				}
			</div>
		)
	}
}

export default connect(mapStateToProps)(GalleryItem);