import React, { Component } from 'react';
import {Link as ScrollLink} from 'react-scroll';
import PropTypes from 'prop-types';

import "./Arrow.css";

class Arrow extends Component {
	render() {
		const destination = this.props.destination;
		const arrDownWrapClass = "arrow_down_wrap " + this.props.blockstyle;
		const arrDownClass =  "arrow_down " + this.props.blockstyle;
		const arrTopWrapClass = "arrow_top_wrap " + this.props.blockstyle;
		const arrTopClass = "arrow_top " + this.props.blockstyle;

		return (
			<div className="arrow" title="Go next block">
				<ScrollLink to={destination} smooth={true} offset={40} duration={500}>
					<div className="arrow_wrap">
						<div className={arrDownWrapClass} >
							<div className={arrDownClass} ></div>
						</div>
						<div className={arrTopWrapClass} >
							<div className={arrTopClass} >
								<div>{this.props.next}</div>
							</div>
						</div>
					</div>
				</ScrollLink>
			</div>
		)
	}
}

Arrow.propTypes = {
	destination: PropTypes.string.isRequired,
	blockstyle: PropTypes.string.isRequired,
	next: PropTypes.string.isRequired
}

export default Arrow;