import React, { Component } from 'react';
import {connect} from "react-redux";

import GalleryItem from "../GalleryItem";
import PagingSection from "../PagingSection";

import "./GallerySection.css";

function mapStateToProps(state) {
	return {
		products: state.main.products,
		groups: state.main.groups,
		allSelectFilter: state.main.allSelectFilter,
		search: state.main.search,
		filterMethod: state.main.filterMethod,
		page: state.main.page,
		perPage: state.main.perPage,
		height: state.main.height
	}
}

class GallerySection extends Component {

	filterProducts() {
		var result = this.props.products
			.slice()
			.filter((element) => {
				if (this.props.filterMethod === "search") {
					return this.searchCheck(element);
				}
				else {
					return this.groupCheck(element);
				}
			});

		return result;
	}

	searchCheck(element) {
		var result = false;
		var search = this.props.search;

		if (search !== "") {
			//search by id
			if ("" + element.id === search) {
				result = true;
			}

			//search by tag
			element.tags.forEach((tag) => {
				if (tag === search) {
					result = true;
				}
			});

			// search by name/title
			Object.keys(element.lang).forEach((language) => {
				const re = RegExp(search, "g");
				if (element.lang[language].title.search(re) !== -1) {
					result = true;
				}
			});
		}
		else {
			result = true;
		}

		return result;
	}

	groupCheck(element) {
		var result = false;

		if(this.props.allSelectFilter) {
			result = true;
		}
		else {
			if (this.props.groups[element.group] && this.props.groups[element.group].value) {
				result = true;
			}
		}

		return result;
	}

	setPageList(list, page, perPage) {
		var startIndex = page * perPage;
		var endIndex = startIndex + perPage;
		if (endIndex > list.length) {
			endIndex = list.length;
		}
		return list.slice(startIndex, endIndex);
	}

	pagesCount(list, perPage) {
		var result = 0;
		var count = list.length;

		while (count > 0) {
			result += 1;
			count -= perPage;
		}

		return result;
	}

	render() {
		var list = this.filterProducts();
		const showPages = list.length > 12 ? true : false;
		var pageList = this.setPageList(list, this.props.page, this.props.perPage);
		var lastPage = this.pagesCount(list, this.props.perPage);
		const blockHeight = this.props.height - 160;
		const gallSectStyle = {minHeight: blockHeight + "px"};

		return (
			<div className="gallery_section" style={gallSectStyle}>
				{
					showPages && <PagingSection lastPage={lastPage} />
				}
				<div className="items">
				{
					pageList.map((element, index) => {
						return <GalleryItem key={index} prodId={element.id}/>
					})					
				}
				</div>
				{
					showPages && <PagingSection lastPage={lastPage} />
				}
			</div>
		)
	}
}

export default connect(mapStateToProps)(GallerySection);