import React, { Component } from "react";
import {Link} from "react-router";
import {connect} from "react-redux";
import {animateScroll} from 'react-scroll';
import {FaChevronUp} from "react-icons/fa";
import PropTypes from "prop-types";

import actions from "../../actions";

import "./SideMainMenu.css";

function mapStateToProps(state) {
	return {
		ui: state.main.ui,
		languages: state.main.languages,
		language: state.main.language
	}
}

class SideMainMenu extends Component {
	constructor(props) {
		super(props);
		this.state = {
			scrollEdge: 320,
			menuClass: " topview"
		}
	}

	componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    };

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll = () => {
    	let currentScroll = 0;
    	if(window.scrollY) {
    		currentScroll = window.scrollY;
    	}
    	if(window.pageYOffset) {
    		currentScroll = window.pageYOffset;
    	}

    	if (this.state && this.state.menuClass === " topview" && currentScroll >= this.state.scrollEdge) {
    		this.setState({
    			menuClass: ""
    		});
    	}

    	if (this.state && this.state.menuClass === "" && currentScroll < this.state.scrollEdge) {
    		this.setState({
    			menuClass: " topview"
    		});
    	}
    }

    changeLanguage = (event) => {
		this.props.dispatch(actions.changeLanguage(event.target.value));
	}

	scrollToTop() {
		animateScroll.scrollToTop({
			duration: 500
		});
	}

	render() {
		const ui = this.props.ui;
		const menuClassName = "side_mainmenu" + this.state.menuClass;
		const toTopClass = "up_button" + this.state.menuClass;

		return (
			<div>
				<div className={menuClassName}>
					<div className="wrapper">
						<div className="mini">
							<div></div>
							<div></div>
							<div></div>
						</div>
						<ul className="list">
							<li><Link to="/">
								<div>
									{ui.mainMenu.main}
								</div>
							</Link></li>
							<li><Link to="/gallery">
								<div>
									{ui.mainMenu.gallery}
								</div>
							</Link></li>
							<li><Link to="/favorits">
								<div>
									{ui.mainMenu.favorits}
								</div>
							</Link></li>
							<li><Link to="/news">
								<div>
									{ui.mainMenu.news}
								</div>
							</Link></li>
							<li><Link to="/articles">
								<div>
									{ui.mainMenu.articles}
								</div>
							</Link></li>
							<li><Link to="/contacts">
								<div>
									{ui.mainMenu.contacts}
								</div>
							</Link></li>
							<li>
								<div className="lang_select">
									<span>{ui.mainMenu.language} :</span>
									<select id="languageSelect" value={this.props.language} onChange={this.changeLanguage}>
										{
											this.props.languages.map((el, i) => {
												return <option key={i} value={el}>{el}</option>
											})
										}
									</select>
								</div>
							</li>
						</ul>
					</div>
				</div>
				<div className={toTopClass} onClick={this.scrollToTop} title={ui.toTop}>
					<FaChevronUp />
				</div>
			</div>
		)
	}
}

SideMainMenu.propTypes = {
	ui: PropTypes.shape({
		mainMenu: PropTypes.shape({
			main: PropTypes.string.isRequired,
			gallery: PropTypes.string.isRequired,
			favorits: PropTypes.string.isRequired
		})
	}),
	languages: PropTypes.array.isRequired,
	language: PropTypes.string.isRequired
}

export default connect(mapStateToProps)(SideMainMenu);