import initialState from "../initialState.js";

const mainPageReducer = (state = initialState, action) => {
	const {type, payload} = action;

	switch(type) {

		//imitation of initial loading resources from server
		case "SITE_INITED": {
			let data = getData(state);

			return {
				...state,
				...data,
				dataLoaded: true
			}
		}

		// change language on site
		case "LANGUAGE_CHANGED": {
			let ui = state.database.uiLang[payload];
			window.localStorage.language = payload;

			return {
				...state,
				ui: ui,
				language: payload
			}
		}

		// change gallery filter checkboxes
		case "FILTER_CHECKBOX_CHANGED": {
			let updGroups = {...state.groups};
			let allSelectValue = true;

			// update checkboxes
			Object.keys(updGroups).forEach((key) => {
				// if not "all" checkbox changed
				if (payload.name !== "all") {
					if (key === payload.name) {
						updGroups[key].value = payload.value;
					}
				}
				// in other case - change all checkboxes
				else {
					updGroups[key].value = payload.value;
				}		
			});

			// update allSelect
			Object.keys(updGroups).forEach((key) => {
				if (updGroups[key].value === false) {
					allSelectValue = false;
				}
			});

			// update localStorage
			window.localStorage.groups = JSON.stringify(updGroups);

			return {
				...state,
				groups: updGroups,
				allSelectFilter: allSelectValue,
				page: 0
			}
		}

		// change gallery search/filter string
		case "SEARCH_CHANGED": {
			return {
				...state,
				search: payload,
				page: 0
			}
		}

		// toggle gallery filter menu
		case "FILTER_MENU_TOGGLED": {
			let value = state.showFilterMenu;
			if (value === "show") {
				value = "hide";
			}
			else {
				value = "show";
			}
			window.localStorage.showFilterMenu = value;

			return {
				...state,
				showFilterMenu: value
			}
		}

		// change filter method in gallery
		case "FILTER_METHOD_CHANGED": {
			return {
				...state,
				filterMethod: payload,
				page: 0
			}
		}

		// add item to user favorits list
		case "ITEM_ADDED_TO_FAVORITS": {
			let products = state.products.slice();
			let favorits = state.favorits.slice();
			if (favorits.indexOf(payload) === -1) {
				favorits.push(payload);

				if (!window.localStorage.favorits) {
					window.localStorage.favorits = JSON.stringify([]);
				}
				
				let local = JSON.parse(window.localStorage.favorits);
				local.push(payload);
				window.localStorage.favorits = JSON.stringify(local);

				for (let i = 0; i < products.length; i++) {
					if (products[i].id === payload) {
						products[i].favorits = true;
						break;
					}
				}
			}

			return {
				...state,
				favorits: favorits,
				products: products
			}
		}

		// remove item from user favorits list
		case "ITEM_REMOVED_FROM_FAVORITS": {
			let products = state.products.slice();
			let favorits = state.favorits.slice();
			let prodIndex = products.findIndex((element) => {
				return element.id === payload;
			});
			let favIndex = favorits.indexOf(payload);

			let local = JSON.parse(window.localStorage.favorits);
			let localIndex = local.indexOf(payload);
			local.splice(localIndex, 1);
			window.localStorage.favorits = JSON.stringify(local);

			favorits.splice(favIndex, 1);
			products[prodIndex].favorits = false;

			return {
				...state,
				products: products,
				favorits: favorits
			}
		}

		// set gallery page value (for paginate)
		case "GALLERY_PAGE_SET": {
			return {
				...state,
				page: payload
			}
		}

		// set product id for personal product page
		case "PRODUCT_PAGE_ID_SET": {
			return {
				...state,
				productPage: payload
			}
		}

		// set post id for personal post page
		case "POST_PAGE_ID_SET": {
			return {
				...state,
				post: payload
			}
		}

		case "WINDOW_RESIZED": {
			return {
				...state,
				width: payload.width,
				height: payload.height
			}
		}

		default: {
			return state;
		}
	}
};

// TOOLS FUNCTIONS
//======================================================================

// data from server (imitation)
const getData = (state) => {
	let data = state.database;
	let result = {};
	let language = data.defaultLanguage;
	let languages = Object.keys(data.uiLang);

	result.width = window.innerWidth;
	result.height = window.innerHeight;

	result.products = getProducts(data);

	result.newProducts = setNewProducts(result.products);
	result.bestSellers = setBestSellers(result.products);
	
	if (window.localStorage.language && window.localStorage.language !== "") {
		language = window.localStorage.language;
	}
	
	result.ui = data.uiLang[language];
	result.language = language;
	result.languages = languages;

	result.groups = getGroups(data);

	// update allSelect
	result.allSelectFilter = true;
	Object.keys(result.groups).forEach((key) => {
		if (result.groups[key].value === false) {
			result.allSelectFilter = false;
		}
	});

	// load showFilterMenu from localStorage
	if (window.localStorage.showFilterMenu) {
		result.showFilterMenu = window.localStorage.showFilterMenu;
	}
	else {
		result.showFilterMenu = "hide";
	}
	
	// set page
	result.page = 0;

	return result;
};

// initial loading products from database & localStorage (favorits)
const getProducts = (data) => {
	let result = data.products.slice();

	// load favorits
	result.forEach((element, index) => {
		if (window.localStorage.favorits) {
			element.favorits = window.localStorage.favorits.indexOf(element.id) >= 0 ? true : false;
		}
		else {
			element.favorits = false;
		}
	}); 

	return result;
}

// initial loading groups from server & localStorage
const getGroups = (data) => {
	let result = data.groups;

	if (window.localStorage.groups) {
		let gr = JSON.parse(window.localStorage.groups);
		Object.keys(result).forEach((key) => {
			if (gr[key] && result[key]) {
				result[key].value = gr[key].value;
			}
		});
	}

	return result;
};

const setNewProducts = (products) => {
	let result = products.slice();

	result.sort((a, b) => {
		return b.data - a.data;
	});

	if (result.length > 6) {
		result = result.slice(0, 6);
	}

	return result;
}

const setBestSellers = (products) => {
	let result = products.slice();

	result.sort((a, b) => {
		return b.sales - a.sales;
	});

	if (result.length > 6) {
		result = result.slice(0, 6);
	}

	return result;
}

export default mainPageReducer;