import React, { Component } from 'react';
import {connect} from "react-redux";
import {Link} from "react-router";
import PropTypes from 'prop-types';
import {Element} from 'react-scroll';

import PostBlock from "../PostBlock";
import Arrow from "../Arrow";

import "./ArticleBlock.css";

function mapStateToProps(state) {
	return {
		ui: state.main.ui
	}
}

class ArticleBlock extends Component {
	render() {
		const blockStyle = "mainpage_block article_block " + this.props.blockstyle;
		const articlesBlockUi = this.props.ui.mainPage.articleBlock;
		const next = this.props.ui.next;

		return (
			<Element name="article_block" className="element">
				<div className={blockStyle}>
					<div className="main_column">
						<p className="description">
							{articlesBlockUi.description}
							<br/>
						</p>
						<h3 className="block_header">{articlesBlockUi.header}</h3>
						<PostBlock post={this.props.post} blockstyle={this.props.blockstyle}/>
						<Link to="/articles">
							<div className="next button">{articlesBlockUi.toArticlesButton}</div>
						</Link>
						<div className="clear_fix"></div>
					</div>
					<Arrow destination="info_block" blockstyle={this.props.blockstyle} next={next}/>
				</div>
			</Element>
		)
	}
}

ArticleBlock.propTypes = {
	post: PropTypes.object.isRequired,
	blockstyle: PropTypes.string.isRequired,
	ui: PropTypes.shape({
		mainPage: PropTypes.shape({
			articleBlock: PropTypes.shape({
				description: PropTypes.string.isRequired,
				header: PropTypes.string.isRequired,
				toArticlesButton: PropTypes.string.isRequired
			})
		}),
		next: PropTypes.string.isRequired
	})
}

export default connect(mapStateToProps)(ArticleBlock);